
import { defineComponent } from 'vue'
import { generateMessage } from '../data'

let id = 0

const messages: any[] = []
for (let i = 0; i < 10000; i++) {
  messages.push(generateMessage())
}

export default defineComponent({
  data() {
    return {
      items: [] as any[],
      search: '',
      streaming: false,
    }
  },

  computed: {
    filteredItems() {
      const { search, items } = this as any
      if (!search) return items
      const lowerCaseSearch = search.toLowerCase()
      return items.filter((i) => i.message.toLowerCase().includes(lowerCaseSearch))
    },
  },

  beforeUnmount() {
    this.streaming = false
  },

  methods: {
    changeMessage(message) {
      Object.assign(message, generateMessage())
    },

    addMessage() {
      for (let i = 0; i < 10; i++) {
        this.items.push({
          id: id++,
          ...messages[id % 10000],
        })
      }
      this.scrollToBottom()

      if (this.streaming) {
        requestAnimationFrame(this.addMessage)
      }
    },

    scrollToBottom() {
      (this.$refs.scroller as any).scrollToBottom()
    },

    startStream() {
      if (this.streaming) return
      this.streaming = true
      this.addMessage()
    },

    stopStream() {
      this.streaming = false
    },
  },
})
